import { currentUser } from "@clerk/nextjs/server";
import { redirect } from "next/navigation";
import React from "react";
import { Navbar } from "@/components/Navbar"; 
import Sidebar from "@/components/Sidebar";

export const metadata = {
  title: "Dashboard | SoloBotAgency",
};

export default async function DashboardLayout({
  children,
}: {
  children: React.ReactNode;
}) {
  // 1. Mandatory Server-Side Authentication Check (KEPT FROM YOUR CODE)
  const user = await currentUser();

  // If user does NOT exist -> redirect("/sign-in")
  if (!user) {
    redirect("/sign-in");
  }
  
  // 2. Structural Layout (UPDATED WITH NEW DESIGN)
  return (
    <div className="flex h-screen bg-[#0B1221] text-slate-100 overflow-hidden font-sans selection:bg-indigo-500/30">
      
      {/* 1. Sidebar (Fixed Left) */}
      <Sidebar />

      {/* 2. Main Content Area */}
      <div className="flex-1 flex flex-col min-w-0 relative">
        
        {/* Navbar (Fixed Top) */}
        <Navbar />

        {/* Page Content (Scrollable) */}
        <main className="flex-1 overflow-y-auto overflow-x-hidden pt-16 bg-gradient-to-b from-[#0B1221] to-[#0f1016] relative">
          {children}

          {/* Premium Footer - Added per request */}
          <div className="hidden lg:block absolute bottom-4 left-6 pointer-events-none select-none z-0">
             <p className="text-[10px] font-medium text-slate-700/50 hover:text-slate-600 transition-colors uppercase tracking-[0.2em] font-mono">
               One Bot. Infinite Tasks.
             </p>
          </div>
        </main>
      </div>
    </div>
  );
}